.basic-slider {
	position: relative;
}
.basic-slider__item {
	background-size: cover;
	background-repeat: no-repeat;
	background-position: center center;
}
.basic-slider__caption {
	padding-top: 288px;
	padding-bottom: 180px;
}
.basic-slider__title {
	margin-bottom: 50px;
	max-width: 500px;
	line-height: 1;
}
.slider-controls__row {
	display: flex;
	justify-content: space-between;
}
.basic-slider__controls {
	position: absolute;
	bottom: 0;
	left: 50%;
	transform: translateX(-50%);
	bottom: 64px;
}
.basic-slider__nav {
	display: flex;
	justify-content: space-between;
	width: 120px;
}
.basic-slider__arrow {
	width: 40px;
	height: 40px;
	display: flex;
	justify-content: center;
	align-items: center;
	border-radius: 40px;
	background-color: $white-clr;
	border: none;
	appearance: none;
	outline: none !important;
	box-shadow: none !important;
	cursor: pointer;
	transform: scale(1);
	transition: .5s ease;
	color: $black-clr;
	@include hover {
		background-color: darken($white-clr, 10%);
		transform: scale(1.1);
	}
}
.basic-slider__dots {
	margin-top: 5px;
}

@media (max-width: 1024px) {
	.basic-slider__caption {
		padding-top: 95px;
		padding-bottom: 98px;
	}
}

@media (max-width: 575px) {
	.basic-slider__caption {
		max-width: 260px;
	}
	.basic-slider__nav {
		display: none;
	}
	.basic-slider__controls {
		bottom: 50px;
	}
	.basic-slider__title {
		margin-bottom: 15px;
		max-width: 272px;
	}
	.basic-slider__text {
		max-width: 260px;
	}
}

/* History slider */

.history-slider {
	.history-slider__item {
		outline: none !important;
	}
	.slick-slide.slick-current {
		.history-slider__year-title {
			opacity: 1;
			visibility: visible;
			transform: translateY(0);
		}
		.history-slider__year-btn {
			font-size: 72px;
			font-weight: bold;
			color: $black-clr;
			&::before {
				transform: translateX(-50%) scale(1);
				width: 62px;
				height: 62px;
				border-radius: 62px;
				background-color: $accent-clr;
				z-index: 1;
				bottom: -25px;
				background-size: 38px 38px;
				background-position: center center;
				background-repeat: no-repeat;
				background-image: url("data:image/svg+xml,%3Csvg width='38' height='38' viewBox='0 0 38 38' fill='none' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink'%3E%3Crect width='38' height='38' fill='url(%23pattern0)'/%3E%3Cdefs%3E%3Cpattern id='pattern0' patternContentUnits='objectBoundingBox' width='1' height='1'%3E%3Cuse xlink:href='%23image0_376_1173' transform='scale(0.00195312)'/%3E%3C/pattern%3E%3Cimage id='image0_376_1173' width='512' height='512' xlink:href='data:image/png;base64,'/%3E%3C/defs%3E%3C/svg%3E%0A");
			}
		}
	}
}

.history-slider__item:last-of-type {
	&.slick-slide.slick-current {
		.history-slider__year-title {
			transform: translateX(calc(-50% - 30px)) translateY(0);
		}
	}
	.history-slider__year {
		.history-slider__year-title {
			flex-direction: row-reverse;
			transform: translateX(calc(-50% - 30px)) translateY(40px);
			img {
				margin-right: 0;
				margin-left: 32px;
			}
		}
		.history-slider__year-btn {
			.line {
				display: block;
				position: absolute;
				bottom: 0;
				right: -44px;
				width: 60px;
				background-color: $grey-light-clr;
				height: 8px;
				border-top-right-radius: 10px;
				border-bottom-right-radius: 10px;
			}
		}
	}
}

.history-slider__item:first-of-type {
	.history-slider__year-btn {
		.line {
			display: block;
			position: absolute;
			bottom: 0;
			left: -50px;
			width: 60px;
			background-color: $grey-light-clr;
			height: 8px;
			border-top-left-radius: 10px;
			border-bottom-left-radius: 10px;
		}
	}
}

.history-slider__year {
	&:focus {
		outline: none !important;
	}
	.history-slider__year-btn {
		border: none;
		background-color: transparent;
		outline: none !important;
		box-shadow: none !important;
		padding: 0;
		color: #dfdfdf;
		font-weight: bold;
		font-size: 24px;
		display: flex;
		align-items: center;
		justify-content: center;
		width: 100%;
		cursor: pointer;
		height: 110px;
		position: relative;
		padding-bottom: 41px;
		transition: .35s;
		@include hover {
			color: $accent-clr;
		}
		.line {
			display: none;
		}
		&::after {
			content: '';
			position: absolute;
			left: 0;
			bottom: 0;
			width: 100%;
			height: 8px;
			background-color: $grey-light-clr;
		}
		&::before {
			content: '';
			position: absolute;
			bottom: -26px;
			left: 50%;
			transform: translateX(-50%) scale(.36);
			background-color: $grey-light-clr;
			// width: 22px;
			// height: 22px;
			width: 62px;
			height: 62px;
			border-radius: 62px;
			transition: transform .35s;
		}
	}
	.history-slider__year-dot {
		position: relative;
	}
	.history-slider__year-title {
		opacity: 0;
		visibility: hidden;
		display: flex;
		margin-left: calc(50% - 5px);
		margin-top: 71px;
		transform: translateY(40px);
		transition: .35s;
		img {
			width: 16px;
			height: 16px;
			margin-right: 32px;
		}
		.history-slider-title {
			font-weight: 700;
			font-size: 24px;
			line-height: 27px;
			padding: 30px;
			border: 5px solid $accent-clr;
		}
	}
}

.history-slider__description-item {
	padding-top: 57px;
	p {
		max-width: 1170px;
		margin: 0 auto;
		font-size: 24px;
		line-height: 36px;
	}
}
